.topbar{
    width: 100%;
    height: 50px;
    background-color: white;
    position: sticky;
    top:0;
}

.topbarWrapper{
    height: 100%;
    padding: 0px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.logo{
    font-weight: boild;
    font-size: 32px;
    color: darkblue;
    cursor: pointer;
}
.topRight{
    display: flex;
    align-items: center;
    cursor: pointer;
}

.topbarIconContainer{
    position: relative;
    margin-right: 10px;
    color: #555;
}
.topIconBag{
    position: absolute;
    top:-5px;
    right: 0px;
    background-color: red;
    color: white;
    border-radius: 50%;
    width: 15px;
    height: 15px;
    align-items: center;
    justify-content: center;
    font-size: 10px;
    display: flex;
}
.topAvatar{
    width: 40px;
    height: 40px;
    border-radius: 50px;
    cursor:pointer;
}